#mapid {
    height: 100vh;
}

body {
    position: relative;
    min-height: 100%;
    margin: 0;
    padding: 0;
    background-color: #f1f1f1;
}

.leaflet-container {
    background: white;
}


#gh-a {
    position: absolute;
    left    : 1em;
    bottom  : 1em;
    z-index : 9999;
}

/*Legend specific*/
.legend {
    padding: 6px 8px;
    font: 14px Arial, Helvetica, sans-serif;
    background: white;
    background: rgba(255, 255, 255, 0.8);
    /*box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);*/
    /*border-radius: 5px;*/
    line-height: 24px;
    color: #555;
}
.legend h4 {
    text-align: center;
    font-size: 16px;
    margin: 2px 12px 8px;
    color: #777;
}

.legend span {
    position: relative;
    bottom: 3px;
}

.legend i {
    width: 18px;
    height: 18px;
    float: left;
    margin: 0 8px 0 0;
    opacity: 0.7;
}

.legend i.icon {
    background-size: 18px;
    background-color: rgba(255, 255, 255, 1);
}

.popup-fixed {
    position: fixed;
    top: auto;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    transform: none !important;
    margin: 0;
    border-radius: 0;
}

@media (min-width: 700px) {
    .popup-fixed {
        width: 250px;
        height: 100% !important;
        left: 0 !important;
        right: auto !important;
        background-color: white;
    }

    .leaflet-popup-content-wrapper {
        height: 100%;
    }
}

.leaflet-popup-tip-container {
    display: none;
}

.leaflet-popup-content-wrapper {
    border-radius: 0;
}

.leaflet-popup-content {
    font-size: 16px;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}

@keyframes border-pulse {
    0% {
        box-shadow: 0 0 0 0 rgb(255, 0, 0);
    }

    70% {
        box-shadow: 0 0 0 20px rgba(255, 0, 0, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(255, 0, 0, 0);
    }
}

.animation {
    border-radius: 50%;
    animation: border-pulse 1.5s infinite;
}

.animation svg {
    animation: rotation 2s infinite linear;
    fill: red;
}